var yourVlSpec = {
  $schema: "https://vega.github.io/schema/vega-lite/v4.json",
  width: 800,
  height: 600,
  selection: {
    highlight: { type: "single", empty: "none", on: "mouseover" }
  },
  data: {
    url: "https://vega.github.io/vega-datasets/data/us-10m.json",
    format: {
      type: "topojson",
      feature: "counties"
    }
  },
  transform: [
    {
      lookup: "id",
      from: {
        data: {
          values: data_total
        },
        key: "id",
        fields: ["MMBTU"]
      }
    }
  ],
  projection: {
    type: "albersUsa"
  },
  mark: {
    type: "geoshape",
    stroke: "red"
  },
  encoding: {
    color: {
      field: "MMBTU",
      type: 'quantitative',
      scale: {
        type: 'log',
        range: 'diverging',
        domainMid: 311789,
        sort: 'descending'
      }
    },
    strokeWidth: {
      condition: [{ selection: "highlight", value: 2 }],
      value: 0
    },
    tooltip: {
      field: "MMBTU",
      format: ",.0f",
      type: 'quantitative'
    }
  }};
vegaEmbed('#energy_consumption_map', yourVlSpec);
